___CSS_LOADER_EXPORT___.push([module.id, `body {
  background-color: rgb(160, 211, 255);
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-button {
  background-color: #0979ad;
  color: white;
  padding: 16px;
  font-size: 16px;
  border: none;
  cursor: pointer;
  width: 200px;
  border: 1px solid #ffffff;
}

.dropdown-button:hover {
  background-color: #0d8fbc;
}

.dropdown-menu {
  position: absolute;
  background-color: #f1f1f1;
  min-width: 200px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;

  visibility: hidden; /* Initially hides the dropdown-menu */
  opacity: 0;
  transform: translateY(0.5rem);
  transition: all 0.1s cubic-bezier(0.16, 1, 0.5, 1);
}

.dropdown-menu li {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  list-style: none;
  background-color: rgb(47, 47, 47);
  border: 1px solid white;
}

.dropdown-menu li:hover {
  background-color: rgb(91, 91, 91);
}

.dropdown-menu li a {
  text-decoration: none;
  color: white;
  display: block;
  padding: 10px;
}

.show {
  visibility: visible;
  opacity: 1;
  transform: translateY(0rem);
}
